import React from 'react';
import { getImg, showName } from '../mabinogi3th_characters';
import Story from '../mabinogi3th_story.json';
import { Character } from '../components/Character';
import { Background } from '../components/Background';
import { MabinogiFrame } from '../components/MabinogiFrame';
import { TEXT_SPEED } from '../index';

export class Game extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      index: 0,
      name: "",
      content: "",
      buttons: [{ event: () => { }, content: "" }],
      img: { url: "", offset: { x: 0, y: 0 } },
    };
  }

  index = -1;
  textIndex = 0;
  gameTexts = Story;

  componentDidMount() {
    Story.forEach((e, i) => {
      e.index = i;
      e.buttons = [this.CreateNextButton(e.index, Story.length)];
      e.img = getImg(e.name, e.face);
      e.name = showName(e.name);
    });
    this.textIndex = 0;
    this.NextText();
  }

  UpdateText() {
    const originalText = this.gameTexts[this.index].content;
    this.textIndex = Math.min(originalText.length, this.textIndex + 1);

    let currentState = Object.assign({}, this.gameTexts[this.index]);
    currentState.content = originalText.slice(0, this.textIndex);

    if (this.textIndex !== originalText.length) {
      currentState.cursor = 'cursorSkip';
      currentState.onClick = () => {
        this.textIndex = originalText.length;
        clearTimeout();
      };
      currentState.buttons = [];
      setTimeout(() => {
        this.UpdateText();
      }, TEXT_SPEED);
    }
    else {
      currentState.cursor = 'cursorStop';
      currentState.onClick = () => { };
    }

    this.setState(currentState);
  }

  NextText() {
    this.index += 1;
    this.textIndex = -1;
    this.UpdateText();
  }

  Close() {
    console.log("Close");
  }

  CreateNextButton(index, length) {
    if (index < length) {
      return {
        event: () => { this.NextText(); },
        content: "繼續"
      };
    }
    else {
      return {
        event: () => { this.Close(); },
        content: "關閉對話視窗"
      };
    }
  }

  render() {
    return (
      <div id="gameContainer">
        <Background></Background>
        <Character img={this.state.img}></Character>
        <div id="frameContainer">
          <MabinogiFrame name={this.state.name} content={this.state.content} buttons={this.state.buttons} onClick={this.state.onClick} cursor={this.state.cursor}></MabinogiFrame>
        </div>
      </div>
    );
  }
}
