import React from 'react';


export class ChatBubble extends React.Component {

  constructor(props) {
    super(props);
    const hue = Math.ceil(360 * Math.random());
    this.firstTime = true;
    this.filterInvert = `hue-rotate(${-hue}deg)`
    this.filter = `hue-rotate(${hue}deg)`;
    this.flex = `${Math.max(this.props.children.length, 6)} 1`;
    this.left = `${this.props.left}em`;
    this.bottom = `${this.props.bottom}em`;
    this.openAnim = `chatBubbleEnter 0.5s cubic-bezier(0.26, 0.71, 0.21, 0.99) 0s forwards`;
    this.closeAnim = `chatBubbleOut 0.3s ease-out forwards`;
    this.staticAnim = `chatBubbleStatic 0s forwards`
    this.currentAnim = '';
    this.state = {
      open: false
    }
  }

  componentDidMount() {

    //把這個打開後動畫不會關閉
    const DEBUG_ALWAYS_ON = false;

    ///此算式為每個框框的顯示時間，數值越大，代表畫面上有框框的數量會越多
    const DurationMin = (DEBUG_ALWAYS_ON) ? 9999999999 : 3000; //最少顯示3秒
    const DurationMax = 5000; //最多顯示8秒
    const DisplayDuration = () => { return DurationMin + DurationMax * Math.random(); };

    ///此算式為框框從關閉到展開需要等待的時間，數值越大，代表框框的空窗期會越長
    const SleepDurationMin = 2000; //最少休息2秒
    const SleepDurationMax = 3000; //最多休息4秒
    const VariationInterval = () => { return SleepDurationMin + SleepDurationMax * Math.random(); };

    const SetOpenEvent = () => {
      setTimeout(() => {
        if (this.props.enable) {
          this.setState({ open: true });
        }
        SetCloseEvent();
      }, VariationInterval());
    };

    const SetCloseEvent = () => {
      setTimeout(() => {
        if (this.props.enable) {
          this.setState({ open: false });
        }
        SetOpenEvent();
      }, DisplayDuration());
    };

    SetOpenEvent();
    this.firstTime = false;
    this.previous = this.state.open;

  }

  componentDidUpdate() {
    this.previous = this.state.open;
  }

  render() {

    let anim = '';
    if (this.props.enable) {
      if (this.firstTime) {
        anim = (this.state.open) ? this.openAnim : this.currentAnim;
      } else {
        anim = (this.state.open) ? this.openAnim : (this.previous !== this.state.open) ? this.closeAnim : this.currentAnim;
      }
    }
    else {
      anim = (this.state.open) ? this.closeAnim : this.staticAnim;
    }
    this.currentAnim = anim;

    const stickerOfChat = {
      width: '3em',
      height: '3em',
      backgroundSize: 'contain',
      backgroundRepeat: 'no-repeat',
      backgroundImage: `url(${this.props.sticker})`,
      position: 'absolute',
      top: '-1em',
      right: '-1em',
      filter: this.filterInvert,
      imageRendering: 'pixelated',
      imageRendering: 'optimizeSpeed',
      imageRendering: '-moz-crisp-edges',
      imageRendering: '-o-crisp-edges',
      msInterpolationMode: 'nearest-neighbor',
    }

    const color = "#816335"
    const smooth = "1px"
    const offset = 1

    const charaName = {
      width: '6em',
      backgroundSize: 'contain',
      backgroundRepeat: 'no-repeat',
      position: 'absolute',
      top: '-0.5em',
      left: '-1em',
      fontSize: 'xx-small',
      letterSpacing: '1px',
      color: "#f5efe7",
      textShadow: `-${offset}px -${offset}px ${smooth} ${color},
                   0    -${offset}px ${smooth} ${color},
                   ${offset}px  -${offset}px ${smooth} ${color},
                   ${offset}px   0   ${smooth} ${color},
                   ${offset}px   ${offset}px ${smooth} ${color},
                   0     ${offset}px ${smooth} ${color},
                   -${offset}px  ${offset}px ${smooth} ${color},
                   -${offset}px  0   ${smooth} ${color}`
    }

    const ChatBubble = {
      animation: anim,
      marginLeft: this.left,
      marginBottom: this.bottom,
      filter: this.filter,
      flex: this.flex,
    }

    const sticker = (this.props.sticker) ? (<div style={stickerOfChat}></div>) : ""
    const name = (this.props.name) ? (<div style={charaName}>{this.props.name}</div>) : ""

    return (
      <div className='chatBubble' style={ChatBubble} key={Date.now()}>
        {sticker}
        {name}
        {this.props.children}
      </div>
    );
  }
}
