window.addEventListener("click", () => {
  canPlaySound = true;
})

export const PlaySound = (src) => {
  if (canPlaySound) {
    var audio = new Audio(src);
    audio.play();
  }
}

export const scrollByPage = (page) => {
  let target = page * window.innerHeight;
  window.scrollTo({
    top: target,
    behavior: "smooth"
  })
}

export const chat = (str, _name, _url) => {
  return { str: str, name: _name, url: _url }
}

