import React from 'react';
import { getFrom } from '../mabinogi3th_characters';
import { ExhibitCharacter } from '../components/ExhibitCharacter';
import logoType from '../img/logotype.png';
import { css, keyframes } from 'glamor';
import { scrollByPage } from '../index';
import arrow from '../img/gui/arrow.svg';
import style from '../css/homepagestyle.css';

export class Cover extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      loaded: false
    }
  }

  componentDidMount() {
    window.addEventListener("load", () => {
      this.setState({
        loaded: true
      })
    })
  }

  render() {
    let chs = [];
    const NameList = {
      rice: { t: 100, d: 0 },
      warm: { t: -100, d: 3 },
      train: { t: -100, d: 5 },
      post: { t: -100, d: 1 },
      banana: { t: 100, d: 2 },
      blue: { t: -100, d: 7 },
      seven: { t: 100, d: 6 },
      deer: { t: 100, d: 8 },
      eight: { t: 100, d: 4 }
    };

    for (const [key, value] of Object.entries(NameList)) {
      const data = getFrom(key, 0, "homepageLayout");
      data.offset.y -= 5;
      chs.push(<ExhibitCharacter img={data} key={key} translate={value.t} delay={value.d * 0.1} enable={this.state.loaded}></ExhibitCharacter>);
    }
    const decoration_back = {
      position: "relative",
      width: "300%",
      height: "20%",
      background: "linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%))",
      right: "72%",
      top: "-95%",
      transform: "rotate(138deg) scale(0,0)",
      animation: (this.state.loaded) ? `ScaleIn 1s 1.8s forwards` : `none`,
    };
    const decoration_front = {
      width: "300%",
      height: "20%",
      background: "linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%))",
      position: "relative",
      right: "9%",
      top: "-80%",
      transform: "rotate(138deg) scale(0,0)",
      animation: (this.state.loaded) ? `ScaleIn 0.5s 1.5s forwards` : `none`,
    };
    const decoration_front_slim = {
      width: "300%",
      height: "5%",
      background: "linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%))",
      position: "relative",
      right: "4%",
      top: "-80%",
      transform: "rotate(138deg) scale(0,0)",
      animation: (this.state.loaded) ? `ScaleIn 0.5s 1.6s forwards` : `none`,
    };
    const Logotype = {
      backgroundImage: `url(${logoType})`,
      width: "40em",
      height: "20em",
      backgroundSize: "contain",
      // left: "calc( 100% - 40em)",
      // top: "calc( 100% - 30em )",
      opacity: 0,
      animation: (this.state.loaded) ? `LogoEnter 1s ease 2.5s forwards, LogoIdle 2s ease-in-out 0s infinite alternate` : `none`,
    };
    const copyRight = {
      width: "52em",
      height: "10em",
      color: "#FFFFFF",
      position: "absolute",
      letterSpacing: "0.1em",
      fontSize: "0.5em",
      lineHeight: "3em",
      right: 0,
      bottom: 0,
      opacity: 0,
      animation: (this.state.loaded) ? `FadeIn 1s 3s forwards` : `none`,
    };
    const preloadMask = {
      position: "fixed",
      top: "0",
      right: "0",
      width: "100%",
      height: "100%",
      backgroundColor: "#FFFFFF",
      backgroundBlendMode: "lighten",
      animation: (this.state.loaded) ? `FadeOut 3s ease forwards` : `none`,
      pointerEvents: "none",
      zIndex: 999
    };
    const nextButton = css({
      width: '6em',
      height: '5em',
      backgroundImage: `url(${arrow})`,
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'contain',
      filter: 'drop-shadow(0 0 6px yellow)',
      opacity: '0',
      animation: (this.state.loaded) ? 'NextButton 0.5s cubic-bezier(0, 0, 0.1, 1.01) infinite alternate, FadeIn 1s ease 3.5s forwards' : `none`,
      pointerEvents: (this.state.loaded) ? `all` : "none"

    })
    const page = {
      width: "100%",
      height: "100%",
    };
    const pageMask = {
      position: "absolute",
      width: "100%",
      height: "100%",
      overflow: "hidden"
    };
    const createParticle = (scale) => {
      const _45 = `${45 * scale}`;
      const _15 = `${15 * scale}`;
      const _30 = `${30 * scale}`;
      const _60 = `${60 * scale}`;
      const _105 = `${105 * scale}`;
      const _90 = `${90 * scale}`;
      const _particleAnim = keyframes({
        '0%': { transform: "scale(0,0)", filter: "brightness(1)" },
        '100%': { transform: "scale(1,1)", filter: "brightness(1.2)" }
      });
      return {
        position: "absolute",
        left: `${Math.random() * 80}%`,
        top: `${Math.random() * 80}%`,
        width: `${58 * scale}px`,
        height: `${90 * scale}px`,
        backgroundColor: "rgb(255, 255, 255)",
        transform: "scale(0,0)",
        clipPath: `path('M 0 ${_45} C ${_15} ${_45} ${_30} ${_30} ${_30} 0 C ${_30} ${_30} ${_45} ${_45} ${_60} ${_45} C ${_30} ${_45} ${_30} ${_90} ${_30} ${_105} C ${_30} ${_90} ${_30} ${_45} 0 ${_45} Z')`,
        animation: `${_particleAnim} ${0.4}s ease-in-out ${Math.random() * 5}s infinite alternate`
      };
    };
    let particles = [];
    let particles_amount = 15 + (Math.random() * 15);
    for (let index = 0; index < particles_amount; index++) {
      particles.push(<div style={createParticle(Math.random() * 0.5 + 0.1)} key={`particles${index}`}></div>);
    }
    const outerGlow = {
      filter: "drop-shadow(0 0 0.75rem yellow)",
      width: "100%",
      height: "100%",
      overflow: "hidden",
      position: "absolute",
      top: 0,
      pointerEvents: "none"
    };
    const pageLayout = {
      width: '40em',
      right: '5%',
      height: '90%',
      overflow: 'hidden',
      position: 'absolute',
      display: 'flex',
      top: '0px',
      placeContent: 'flex-end',
      flexFlow: 'column nowrap',
      alignItems: 'center'
    };
    return (
      <div id='homepage-cover' style={page}>
        <div style={pageMask}>
          <div style={decoration_back}></div>
        </div>
        {chs}
        <div style={decoration_front}></div>
        <div style={decoration_front_slim}></div>

        <div style={copyRight}>
          本企劃有部分素材源自由韓國devCat所開發的遊戲新瑪奇Mabinogi。<br></br>
          除外則為Mabinogi之中玩家角色的二次創作。<br></br>
          本企劃為貝婷的疊字友友們共同製作。
        </div>
        <div style={outerGlow}>
          {particles}
        </div>

        <div style={pageLayout}>
          <div style={Logotype}></div>
          <div className='FlexHoverToScale'>
            <div {...nextButton} onClick={() => { scrollByPage(1); }}></div>
          </div>
        </div>

        <div style={preloadMask}></div>
      </div>
    );
  }
}
