import React from 'react';
import Techduinn from '../img/background/techduinn.png';


export class Background extends React.Component {
  render() {
    const style = {
      position: "absolute",
      height: "min(100%, 30em)",
      width: "min(100%, 40em)",
      backgroundRepeat: "No-Repeat",
      backgroundSize: "100%",
      backgroundImage: `url(${Techduinn})`,
    };
    const Flex = {
      display: "flex",
      alignItems: "center",
      flexFlow: "row nowrap",
      placeContent: "center",
      width: "100%",
      height: "100%",
      position: "absolute"
    };
    return (
      <div style={Flex}>
        <div style={style}></div>
      </div>
    );
  }
}
