import React from 'react';
import Down from '../se/button_down.wav';
import Confirm from '../se/button_confirm.wav';
import Cancel from '../se/button_cancel.wav';
import { PlaySound } from '../index';

export class MabinogiButton extends React.Component {
  render() {
    const onClick = () => {
      this.props.onclick();
    };
    const onMouseUp = () => {
      PlaySound(Confirm);
    };
    const onMouseDown = () => {
      PlaySound(Down);
    };
    const onMouseLeave = () => {
      PlaySound(Cancel);
    };
    return (
      <button className="mabinButton" onMouseDown={onMouseDown} onClick={onClick} onMouseUp={onMouseUp} onMouseLeave={onMouseLeave}>{this.props.children}</button>
    );
  }
}
