import React from 'react';
import { getImg, NameList, getFileName } from '../mabinogi3th_characters';
import { MabinogiFrame } from "../components/MabinogiFrame";
import { Background } from "../components/Background";
import { Character } from "../components/Character";

export class Editor extends React.Component {

  constructor(props) {
    super(props);

    let defaultState = {
      originalData: [{ characterName: "", fileName: "" }],
      showCharacter: [],
      editCharacter: [],
      offset: [{ x: 0, y: 0 }],
      mousePos: { x: 0, y: 0 },
      scale: [0]
    };

    let index = 0;
    for (const [key, value] of Object.entries(NameList)) {
      const data = getImg(key, 0);
      defaultState.showCharacter[index] = false;
      defaultState.originalData[index] = { characterName: key, fileName: data.url };
      defaultState.offset[index] = data.offset;
      defaultState.scale[index] = data.scale;
      index++;
    }

    this.state = defaultState;

  }

  render() {

    const DISPLAYBARS = false;
    const DRAGABLE = true;
    let Characters = [];
    let inputField = [];
    let index = 0;

    const style = {
      color: "#FFFFFF"
    };

    for (const [key, value] of Object.entries(NameList)) {

      const ImgData = getImg(key, 0);
      const offset = this.state.offset[index];
      const scale = this.state.scale[index];
      const thisIndex = index;
      ImgData.offset = offset;
      ImgData.scale = scale;

      if (this.state.showCharacter[index]) {
        Characters[index] = (
          <Character id={key} img={ImgData} key={key}></Character>
        );
      }

      const DisplaySelect = (e) => {
        let state = this.state;
        state.showCharacter[thisIndex] = e.target.checked;
        this.setState(state);
      };

      const Offset_Y_Change = (e) => {
        let state = this.state;
        state.offset[thisIndex] = { x: e.target.value, y: state.offset[thisIndex].y };
        this.setState(state);
      };

      const Offset_X_Change = (e) => {
        let state = this.state;
        state.offset[thisIndex] = { x: state.offset[thisIndex].x, y: e.target.value };
        this.setState(state);
      };

      const Offset_S_Change = (e) => {
        let state = this.state;
        state.scale[thisIndex] = e.target.value;
        this.setState(state);
      };

      const DragCharacter = (e) => {
        let state = this.state;
        if (state.editCharacter[thisIndex] !== true) return;
        let deltaPos = { x: state.mousePos.x - e.x, y: state.mousePos.y - e.y };
        let Pos = {
          x: Number(state.offset[thisIndex].x) - deltaPos.y / 10,
          y: Number(state.offset[thisIndex].y) - deltaPos.x / 10
        }
        state.offset[thisIndex] = Pos;
        state.mousePos = { x: e.x, y: e.y }
        this.setState(state);
      }

      const Scale = (e) => {
        let state = this.state;
        if (state.editCharacter[thisIndex] !== true) return;
        let addScale = 0
        addScale = (e.deltaY > 0) ? 0.05 : -0.05;
        state.scale[thisIndex] = Number(state.scale[thisIndex]) + addScale;
        this.setState(state);
      }

      const StartControllXY = (e) => {
        let state = this.state;
        state.mousePos = { x: e.x, y: e.y }
        this.setState(state);
        document.addEventListener("mousemove", DragCharacter);
      }
      const EndControllXY = () => {
        document.removeEventListener("mousemove", DragCharacter);
      }

      const ForceDisplay = () => {
        const display = document.getElementById("show" + key);
        if (!display.checked) {
          display.checked = true;
          let state = this.state;
          state.showCharacter[thisIndex] = true;
          this.setState(state);
        }
      }

      const StartEditing = (e) => {
        let state = this.state;

        if (e.target.checked) {
          ForceDisplay();
          const edit = document.getElementsByClassName("editing");
          
          document.addEventListener("wheel", Scale);
          document.addEventListener("mousedown", StartControllXY);
          document.addEventListener("mouseup", EndControllXY);

          for (let index = 0; index < edit.length; index++) {
            if (index !== thisIndex) {
              const element = edit[index];
              element.checked = false;
              state.editCharacter[index] = false;
            }
          }
        }

        state.editCharacter[thisIndex] = e.target.checked;
        this.setState(state);
      }

      if (DISPLAYBARS) {
        inputField[index] = (
          <div style={style} key={key}>
            <input id={"show" + key} type="checkbox" defaultChecked={this.state.showCharacter[index]} onChange={DisplaySelect}></input>
            <label form={key}>{value}</label>
            <label form={key}>X</label>
            <input type="range" min="-30" max="99" step="1" defaultValue={offset.x} onChange={Offset_X_Change}></input>
            <label form={key}>Y</label>
            <input type="range" min="0" max="99" step="1" defaultValue={offset.y} onChange={Offset_Y_Change}></input>
            <label form={key}>Scale</label>
            <input type="range" min="0" max="3" step="0.1" defaultValue={scale} onChange={Offset_S_Change}></input>
          </div>
        );
      }
      else if (DRAGABLE) {
        inputField[index] = (
          <div style={style} key={key}>
            <input id={"show" + key} type="checkbox" defaultChecked={this.state.showCharacter[index]} onChange={DisplaySelect}></input>
            <input className="editing" type="checkbox" name='drag' onChange={StartEditing}></input>
            <label form={key}>{value}</label>
          </div>
        );
      }

      index++;
    }

    const inputStyle = {
      "position": "absolute",
      "right": "0"
    };

    const Export = () => {
      let exportJson = [];

      for (let index = 0; index < this.state.originalData.length; index++) {
        exportJson.push({
          characterName: this.state.originalData[index].characterName,
          fileName: getFileName(this.state.originalData[index].characterName),
          offset: this.state.offset[index],
          scale: this.state.scale[index]
        });
      }

      let a = document.createElement('a');
      let url = window.URL.createObjectURL(new Blob([JSON.stringify(exportJson)], { type: 'application/json' }));
      let filename = 'export_mabinogi3th_characters.json';
      a.href = url;
      a.download = filename;
      a.click();
      window.URL.revokeObjectURL(url);

    };

    return (
      <div id="gameContainer" >
        <Background></Background>
        {Characters}
        <div id="frameContainer" >
          <MabinogiFrame name="Test" content="這是用來定位的對話框." buttons={[]} onClick={() => { }} cursor={''}></MabinogiFrame>
        </div>
        <div style={inputStyle}>
          {inputField}
          <input type="button" value="Export!!!!" onMouseDown={Export}></input>
        </div>
      </div >
    );
  }
}
