import React from 'react';

export class MabinogiName extends React.Component {
  render() {
    const size = 2;
    const style = {
      color: "#bfdbe3",
      textShadow: `#000 0px 0px ${size}px, #000 0px 0px ${size}px, #000 0px 0px ${size}px, #000 0px 0px ${size}px, #000 0px 0px ${size}px, #000 0px 0px ${size}px`,
      WebkitFontSmoothing: "antialiased",
    };
    return (
      <div style={style}>{this.props.children}</div>

    );
  }
}
