import datas from './mabinogi3th_characters.json'
import homepage from './mabinogi3th_characters_homePageSorting.json'

let preloadImg = []

const converCharacterData = (characterName = "", fileName = "", offset = { x: 0, y: 0 }, scale = 0) => {
    let url = require(`./characters/${fileName}.png`).default
    if (!preloadImg.includes(url)) {
        preloadImg.push({ url: url, state: false });
    }
    return { url: url, offset: offset, scale: scale }
}

const rigistData = (targets = [{ characterName: "", fileName: "", offset: { x: 0, y: 0 }, scale: 0 }]) => {
    let output = {};
    targets.forEach(element => {
        element.offset = element.offset || { x: 0, y: 0 }
        element.scale = element.scale || 0
        let data = converCharacterData(element.characterName, element.fileName, element.offset, element.scale)

        if (output[element.characterName] === undefined) {
            Object.defineProperty(output, element.characterName, {
                value: [],
                writable: true
            });
        }

        output[element.characterName].push(data)
    });
    return output;
}

let rigistDatas = {
    "homepageLayout": rigistData(homepage),
    "story": rigistData(datas)
}

export const Preload = () => {
    return new Promise((resolve) => {

        const check = () => {
            let done = 0;
            preloadImg.forEach(e => {
                if (e.state) done++;
            })
            if (done === preloadImg.length) {
                console.log("Alldone");
                resolve();
            }
        }
        preloadImg.forEach(e => {
            const img = new Image();
            img.onload = () => { e.state = true; check(); }
            img.src = e.url;
        })
    })
}

const empty = { url: "", offset: { x: 0, y: 0 }, scale: 0 }

export const getImg = (str, id) => {
    return getFrom(str, id, "story")
}

export const getFrom = (str, id, dataCollectionName) => {
    if (str === '') return empty
    let _ch = rigistDatas[dataCollectionName][str]
    if (_ch === undefined) return empty
    let _element = _ch[id] || _ch[0]
    let _offset = _element.offset
    let _url = _element.url;
    let _scale = _element.scale;
    return { url: _url, offset: _offset, scale: _scale }
}

export const getFileName = (str) => {
    for (let index = 0; index < datas.length; index++) {
        const element = datas[index];
        if (element.characterName === str) {
            return element.fileName
        }
    }
    return ""
}

