import { css } from 'glamor';
import React from 'react';
import { isInViewport, scrollByPage } from '../index';
import { ChatBubble } from "./ChatBubble";

export class SigletoneExhibit extends React.Component {

  pageID = 0;

  constructor(props) {
    super(props);
    this.TitleSpans = [];

    let __state = {
      chatsState: [],
      key: 0,
      play: false,
    };

    this.pageID = Math.random() * 10003;

    if (this.props.chats !== undefined) {
      const shuffle = (array) => {
        for (let i = array.length - 1; i > 0; i--) {
          let j = Math.floor(Math.random() * (i + 1));
          [array[i], array[j]] = [array[j], array[i]];
        }
        return array.slice();
      };
      const createRandomArray = (a, b, step = 1) => {
        const min = (a >= b) ? b : a;
        const max = (min === b) ? a : b;
        let _return = [];
        for (let i = min; i < max; i += step) {
          _return.push(i);
        }
        return shuffle(_return);
      };
      let left = createRandomArray(-5, 0);
      let bottom = createRandomArray(10, 20, 2);
      const getValue = (of = [], index) => {
        const i = index % of.length; //循環取得
        const val = of[i];
        return val + ((Math.random() * 0.5) * ((Math.random > 0.5) ? -1 : 1)); //取得±0.5
      };
      this.left = [];
      this.bottom = [];
      this.str = [];

      for (let index = 0; index < this.props.chats.length; index++) {
        const str = this.props.chats[index];
        __state.chatsState.push(false);
        this.left.push(getValue(left, index));
        this.bottom.push(getValue(bottom, index));
        this.str.push(str);
      }
    }

    if (this.props.picOffset !== undefined) {
      this.picOffset = this.props.picOffset;
    } else {
      this.picOffset = ["100%", "100%"];
    }
    if (this.props.picUrl !== undefined) {
      this.picUrl = this.props.picUrl;
    }

    this.state = __state;
  }

  componentDidMount() {

    window.addEventListener("scroll", s => {
      const element = document.getElementById(this.pageID);
      if (element === undefined || element === null)
        return;
      if (isInViewport(element)) {
        if (!this.state.play)
          this.setAnimation(true);
      }
      else {
        if (this.state.play)
          this.setAnimation(false);
      }
    });
  }


  setAnimation(bool) {
    let state = this.state;
    state.play = bool;
    state.key = Date.now;
    this.setState(state);
  }

  anim(classname) {
    if (this.state.play) { return classname; }
    else
      return classname + ' noAnim';
  }

  render() {

    let chats = [];
    if (this.props.chats !== undefined) {
      for (let index = 0; index < this.props.chats.length; index++) {
        const chat = this.props.chats[index];
        chats.push(
          <ChatBubble
            enable={this.state.play}
            left={this.left[index]}
            bottom={this.bottom[index]}
            key={index + chat.str + chat.name + chat.url}
            name={chat.name}
            sticker={chat.url}
          >
            {chat.str}
          </ChatBubble>
        );
      }
    }

    let TitleSpan = []
    if (this.props.title !== undefined) {
      for (let index = 0; index < this.props.title.length; index++) {
        const style = {
          display: "inline-block",
          transform: "scale(0, 0)",
          animation: (this.state.play) ?
            `ScaleIn-text 0.3s cubic-bezier(0.04, 0.83, 0.35, 1.58) ${(index * 0.1)}s forwards` :
            `WaveOut 0.3s ease forwards`
        };
        const text = this.props.title.slice(index, index + 1);
        TitleSpan.push(<span style={style} key={"_title" + index + "_" + text}>{text}</span>);
      }
    }

    const page = {
      flex: 1,
      display: "flex",
      width: "100%",
      height: "100%",
    };

    const lcolumn = {
      flex: '1 1 0%',
      display: 'flex',
      placeContent: 'center',
      alignItems: 'flex-end',
      flexDirection: 'column',
      alignContent: 'flex-end',
      flexWrap: 'wrap',
      justifyContent: 'space-evenly'
    };
    const lcolumn_mirror = {
      flex: '1 1 0%',
      display: 'flex',
      placeContent: 'center',
      alignItems: 'flex-end',
      flexDirection: 'column',
      alignContent: 'flex-start',
      flexWrap: 'wrap',
      justifyContent: 'space-evenly'
    };
    const rColumn = {
      flex: '0.9 1 0%',
      display: 'flex',
      placeContent: 'flex-end center',
      flexDirection: 'column',
      alignItems: 'flex-end',
      justifyContent: 'flex-end',
      alignContent: 'flex-end'
    };

    const pic = {
      backgroundImage: `url(${this.picUrl})`,
      backgroundRepeat: "no-repeat",
      backgroundPosition: `${this.picOffset[0]} ${this.picOffset[1]}`,
      backgroundColor: `transparent`
    };

    const buttonOffset = css({
      marginTop: "3em"
    })

    if (this.props.mirror) {
      return (
        <div style={page}>
          <div id={this.pageID} key={"infopic" + this.state.key} className={'infoPictureArea'} style={pic}></div>
          <div style={rColumn}>
            <div className='chats'>{chats}</div>
          </div>
          <div style={lcolumn_mirror}>
            <div key={"infotitle" + this.state.key} className={'infoTitle'}>
              {TitleSpan}
            </div>
            <div key={"infocontent" + this.state.key} className={`infocontent ${(this.state.play) ? 'infocontentIn' : 'infocontentOut'}`}>
              {this.props.children}
              <div className={`FlexHoverToScale ${buttonOffset}`}>
                <div className='FlexNextButton' onClick={() => { scrollByPage(this.props.page + 1); }}></div>
              </div>
            </div>
          </div>
        </div>
      );
    }
    else {
      return (
        <div style={page}>
          <div id={this.pageID} className={'infoPictureArea'} style={pic}></div>
          <div style={lcolumn}>
            <div key={"infotitle" + this.state.key} className={'infoTitle'}>
              {TitleSpan}
            </div>
            <div key={"infocontent" + this.state.key} className={`infocontent ${(this.state.play) ? 'infocontentIn' : 'infocontentOut'}`}>
              {this.props.children}
              <div className={`FlexHoverToScale ${buttonOffset}`}>
                <div className='FlexNextButton' onClick={() => { scrollByPage(this.props.page + 1); }}></div>
              </div>
            </div>
          </div>
          <div style={rColumn}>
            <div className='chats'>{chats}</div>
          </div>
        </div>
      );
    }
  }
}
