import { css, keyframes } from 'glamor';
import React from 'react';

export class ExhibitCharacter extends React.Component {

  render() {

    const keys = keyframes({
      '0%': { transform: `translate(0px,${this.props.translate}px)`, opacity: 0 },
      '100%': { transform: "translate(0px,0px)", opacity: 1 }
    })

    const brightInKeys = keyframes({
      '0%': { filter: "contrast(0) brightness(10)" },
      '100%': { filter: "contrast(0) brightness(none)" }
    })

    const exhibitCharacterAnimation = css({
      animation: `${keys} 3s cubic-bezier(0, 0.68, 0, 0.99) ${this.props.delay}s, 
      ${brightInKeys} 1.5s ease`,
      animationFillMode: "forwards",
    })

    const NoAnimation = css({
      animation: `none`,
    })

    const exhibitCharacter = css({
      "--offset-top": this.props.img.offset.x + "em",
      "--offset-left": this.props.img.offset.y + "em",
      "--offset-scale": this.props.img.scale,
      position: "absolute",
      width: "calc( 100% - var(--offset-left) )",
      height: "100%",
      left: "var(--offset-left)",
      backgroundImage: `url(${this.props.img.url})`,
      backgroundSize: "auto calc( 100% * var(--offset-scale) )",
      backgroundRepeat: "no-repeat",
      backgroundPositionY: "var(--offset-top)",
      opacity: 0,
      zIndx: 2,
    });

    const anim = (this.props.enable) ? exhibitCharacterAnimation : NoAnimation

    return (
      <div {...exhibitCharacter} {...anim} key={`${Date.now}-in-cover-ch`}></div>
    );
  }
}
