import React from 'react';
import { MabinogiButton } from './MabinogiButton';
import { MabinogiContent } from './MabinogiContent';
import { MabinogiName } from './MabinogiName';


export class MabinogiFrame extends React.Component {
  render() {
    let buttonJSX = [];

    for (let index = 0; index < this.props.buttons.length; index++) {
      const button = this.props.buttons[index];
      if (button.content === "")
        continue;
      buttonJSX[index] = (<MabinogiButton key={button.content} onclick={button.event}>{button.content}</MabinogiButton>);
    }

    return (
      <div id="mabinFrame" className={this.props.cursor} onClick={this.props.onClick}>
        <MabinogiName>{this.props.name}</MabinogiName>
        <MabinogiContent>{this.props.content}</MabinogiContent>
        <div id="mabinButtonArea">
          {buttonJSX}
        </div>
      </div>
    );
  }
}
