import { css, keyframes } from 'glamor';
import React from 'react';
import { chat, isInViewport, scrollByPage } from '../index';
import { ChatBubble } from "../components/ChatBubble";
import eight from '../characters/eight_1.png'

// 1. 顯示角色
// 2. 顯示對應的文字顯示/ChatBubble/特寫
// 3. 可以透過下方欄切換
export class CharacterPage extends React.Component {

  constructor(props) {
    super(props)
    this.state = { play: false }

    const shuffle = (array) => {
      for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array.slice();
    };
    const createRandomArray = (a, b, step = 1) => {
      const min = (a >= b) ? b : a;
      const max = (min === b) ? a : b;
      let _return = [];
      for (let i = min; i < max; i += step) {
        _return.push(i);
      }
      return shuffle(_return);
    };
    let left = createRandomArray(-5, 0);
    let bottom = createRandomArray(10, 20, 2);
    const getValue = (of = [], index) => {
      const i = index % of.length; //循環取得
      const val = of[i];
      return val + ((Math.random() * 0.5) * ((Math.random > 0.5) ? -1 : 1)); //取得±0.5
    };

    let ch_chats = [
      chat("八!"),
      chat("八八!"),
      chat("八八八!"),
      chat("八八八八!"),
      chat("八八八八八!"),
    ];

    this.ch_chats = []
    ch_chats.forEach((e, i) => {
      let t = { chat: e, pos: { l: 0, b: 0 } }
      t.pos.l = getValue(left, i)
      t.pos.b = getValue(bottom, i)
      this.ch_chats.push(t)
    })
  }

  componentDidMount() {
    window.addEventListener("scroll", s => {
      const element = document.getElementById('characterpage');
      if (element === undefined || element === null)
        return;
      if (isInViewport(element)) {
        if (!this.state.play) this.setState({ play: true })
      }
      else {
        if (this.state.play) this.setState({ play: false })
      }
    });
  }
  render() {

    const height = `${100 / 3}%`
    const page = css({
      flex: 1,
      display: "flex",
      width: '100%',
      height: '100%',
      overflow: 'hidden'
    });
    const Container = css({
      width: '100%',
      height: '100%',
    })
    const CenterCharacter = css({
      height: `${100 / 3}%`,
      width: '100%',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '50%',
      backgroundImage: `url(${eight})`,
      backgroundPositionX: 'center',
      backgroundPositionY: '5em',
      position: 'absolute'
    })
    const CloseUp_Fading = keyframes({
      '0%': { opacity: 0 },
      '10%': { opacity: 0.5 },
      '70%': { opacity: 0.5 },
      '100%': { opacity: 0 }
    })
    const CloseUp_1_anim = keyframes({
      '0%': { backgroundPositionY: '-2em' },
      '100%': { backgroundPositionY: '42em' }
    })

    //差分展示
    const CloseUp_1_Anim = (this.state.play) ? `${CloseUp_Fading} 3s ease-in-out infinite alternate` : 'none'
    const CloseUp_1 = css({
      height: height,
      width: '100%',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '100%',
      backgroundImage: `url(${eight})`,
      backgroundPositionX: '-14em',
      backgroundPositionY: '1em',
      position: 'absolute',
      opacity: 0,
      WebkitMaskImage: 'linear-gradient(0, transparent,white,white, white, white,white,transparent)',
      animation: CloseUp_1_Anim
    })
    const CloseUp_2_anim = keyframes({
      '0%': { backgroundPositionY: '-100em' },
      '100%': { backgroundPositionY: '-10em' }
    })
    const CloseUp_2_Anim = (this.state.play) ? `${CloseUp_2_anim} 20s ease-in-out infinite, ${CloseUp_Fading} 20s ease-in-out infinite alternate` : 'none'
    const CloseUp_2 = css({
      height: height,
      width: '100%',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '60%',
      backgroundImage: `url(${eight})`,
      backgroundPositionX: '120%',
      backgroundPositionY: '-75em',
      position: 'absolute',
      opacity: 0,
      WebkitMaskImage: 'linear-gradient(0, transparent,white,white, white, white,white,transparent)',
      animation: CloseUp_2_Anim
    })

    const title = "夜葬八八"
    let TitleSpan = []
    if (title !== undefined) {
      for (let index = 0; index < title.length; index++) {
        const style = {
          display: "inline-block",
          transform: "scale(0, 0)",
          animation: (this.state.play) ?
            `ScaleIn-text 0.3s cubic-bezier(0.04, 0.83, 0.35, 1.58) ${2 + (index * 0.1)}s forwards` :
            `WaveOut 0.3s ease forwards`
        };
        const text = title.slice(index, index + 1);
        TitleSpan.push(<span style={style} key={"_title" + index + "_" + text}>{text}</span>);
      }
    }

    const infocontainer = css({
      width: '60%',
      right: '-17%',
      top: '77%',
      position: 'absolute',
      display: 'flex',
      alignItems: 'flex-start',
      justifyContent: 'flex-start',
      flexDirection: 'column',
      alignContent: 'flex-start',
      flexWrap: 'wrap'
    })
    const chatContainer = css({
      width: '60%',
      display: 'flex',
      placeContent: 'flex-end center',
      alignItems: 'flex-end',
      justifyContent: 'flex-end',
      alignContent: 'flex-end',
      flexDirection: 'row',
    });

    let chats = []
    for (let index = 0; index < this.ch_chats.length; index++) {
      const chat = this.ch_chats[index].chat;
      const pos = this.ch_chats[index].pos;
      chats.push(
        <ChatBubble
          enable={this.state.play}
          left={pos.l}
          bottom={pos.b}
          key={index + chat.str + chat.name + chat.url}
          name={chat.name}
          sticker={chat.url}
        >
          {chat.str}
        </ChatBubble>
      );
    }

    return (
      <div {...page} id='characterpage'>
        <div {...Container}>
          <div {...CloseUp_1}></div>
          <div {...CloseUp_2}></div>
          <div {...CenterCharacter}></div>
        </div>
        <div className={infocontainer}>
          <div className={'infoTitle'} style={{
            display: 'flex',
            justifyContent: 'flex-start',
            marginBottom: '0'
          }}>
            {TitleSpan}
          </div>
          <div
            key={"infocontent" + this.state.key}
            className={`infocontent ${(this.state.play) ? 'infocontentIn' : 'infocontentOut'}`}>
            <p>
              繪師：鴿子波波寶貝<br></br>
              第二屆、第三屆愛爾琳小天使<br></br>
              我ㄉ老公，快樂ㄉ老公
            </p>
          </div>
          <div className={chatContainer}>
            {chats}
          </div>
        </div>
      </div>
    )
  }
}
