import React from 'react';
import Mask from '../img/mask.png';


export class Character extends React.Component {
  render() {
    // height: calc( 100%);
    // width: calc( 100% - var(--offset-left) );
    // left: var(--offset-left);
    // top: var(--offset-top);

    const characterMask = {
      width: "100%",
      height: "100%"
    };
    // const characterImage = {
    //   "--offset-top": this.props.img.offset.x + "em",
    //   "--offset-left": this.props.img.offset.y + "em",
    //   "--offset-scale": this.props.img.scale,
    //   position: "absolute",
    //   width: "100%",
    //   height: "100%",
    //   backgroundImage: `url(${this.props.img.url})`,
    //   backgroundSize: "calc( 100% * var(--offset-scale) )",
    //   backgroundRepeat: "no-repeat",
    //   backgroundPositionX: "var(--offset-left)",
    //   backgroundPositionY: "var(--offset-top)"
    // }
    const characterImage = {
      "--offset-top": this.props.img.offset.x + "em",
      "--offset-left": this.props.img.offset.y + "em",
      "--offset-scale": this.props.img.scale,
      position: "absolute",
      width: "calc( 100% - var(--offset-left) )",
      height: "100%",
      left: "var(--offset-left)",
      backgroundImage: `url(${this.props.img.url})`,
      backgroundSize: "auto calc( 100% * var(--offset-scale) )",
      backgroundRepeat: "no-repeat",
      backgroundPositionY: "var(--offset-top)"
    };
    const mask = {
      position: "absolute",
      height: "25em",
      backgroundImage: `url(${Mask})`,

      width: "100%",
      backgroundSize: "30em",
      mixBlendMode: "multiply"
    };
    const maskFlex = {
      display: "flex",
      alignItems: "flex-end",
      width: "100%",
      height: "100%",
      position: "absolute",
      alignContent: "center",
      flexDirection: "row",
      flexWrap: "nowrap",
      justifyContent: "center"
    };
    return (
      <div>
        <div style={characterMask}>
          <div id={this.props.id} style={characterImage}>        </div>
        </div>
        <div style={maskFlex}>
          <div style={mask}>        </div>
        </div>
      </div>
    );
  }
}
